.button {
  @include button;

  &:hover {
    @include button-hover;
  }
}

.button {
  &--outline {
    @include button-outline;
  }
  &--white {
    @include button-white;
  }
  &--surface {
    @include button-surface;
  }
  &--square {
    @include button-square;
  }
  &--backgroundless, .button-backgroundless & {
    &:not(:hover):not(:focus) {
      background-color: transparent;
      color: inherit;
      border-color: transparent;
    }
  }
}
